import React from 'react';
import { Link } from 'react-scroll';

import exit from '../../assets/images/exit.svg';
import './SideDrawer.css';

const SideDrawer = ({ closeButtonClick, show }) => {
    let drawerClasses = 'side-drawer white-bg sticky'
    if (show) {
        drawerClasses = 'side-drawer white-bg sticky open'
    }
    return (
        <div className={drawerClasses}>
            <div className="exit-btn-cnt full-width"><img className="exit-btn pointer" src={exit} alt="Выйти из меню" onClick={closeButtonClick} /></div>
            <ul className="flex-column">
                <Link activeClass="active" to="home" spy={true} smooth={true} offset={0} duration={500} onClick={closeButtonClick}>
                    <li className="pointer">главная</li>
                </Link>
                <Link activeClass="active" to="about" spy={true} smooth={true} offset={0} duration={500} onClick={closeButtonClick}>
                    <li className="pointer">о школе</li>
                </Link>
                <Link activeClass="active" to="events" spy={true} smooth={true} offset={0} duration={500} onClick={closeButtonClick}>
                    <li className="pointer">программа</li>
                </Link>
                <Link activeClass="active" to="faq" spy={true} smooth={true} offset={0} duration={500} onClick={closeButtonClick}>
                    <li className="pointer">FAQ</li>
                </Link>
                <Link activeClass="active" to="contacts" spy={true} smooth={true} offset={0} duration={500} onClick={closeButtonClick}>
                    <li className="pointer">контакты</li>
                </Link>
            </ul>
        </div>
    );
};

export default SideDrawer;