import React, { useState, useRef } from 'react';
import { useSpring, useTransition, useChain, animated, config } from 'react-spring';

import clockViolet from '../../assets/images/clock-violet.svg';
import clockBlue from '../../assets/images/clock-blue.svg';
import './EventCard.css';

const info = [
    {
        id: 0,
        name: 'Rare Wind',
        description: '#a8edea → #fed6e3',
        css: 'linear-gradient(135deg, #a8edea 0%, #fed6e3 100%)',
        height: 200
    },
    {
        id: 1,
        name: 'Saint Petersburg',
        description: '#f5f7fa → #c3cfe2',
        css: 'linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%)',
        height: 400
    },
];

const EventCard = ({ evName, profName, time, groupNum }) => {

    const [open, setOpen] = useState(false);
    const springRef = useRef()
    const propsSpring = useSpring({
        ref: springRef,
        config: config.stiff,
        from: { opacity: 1 },
        to: { opacity: open ? 0 : 1}
    });

    const transRef = useRef()
    const transitions = useTransition(open ? info : [], item => item.id, {
        ref: transRef,
        unique: true,
        trail: 400 / info.length,
        from: { opacity: 0, transform: 'scale(0)' },
        enter: { opacity: 1, transform: 'scale(1)' },
        leave: { opacity: 0, transform: 'scale(0)' }
    });

    useChain(open ? [springRef, transRef] : [transRef, springRef], [0, open ? 0.1 : 0.6])

    let cardStyle = "card ev-right-margin line-height white-bg violet-card violet padded-container";
    let clockTextStyle = "violet align-with-clock";
    // let evMoreStyle = "ev-more-1";
    if (groupNum === 2) {
        cardStyle = "card ev-right-margin line-height white-bg violet-blue-card padded-container";
        clockTextStyle = "violet-blue align-with-clock";
        // evMoreStyle = "ev-more-2";
    }
    if (groupNum === 3) {
        cardStyle = "card ev-right-margin line-height white-bg blue-card blue padded-container";
        clockTextStyle = "blue align-with-clock";
        // evMoreStyle = "ev-more-3";
    }

    return <animated.div style={propsSpring} className={cardStyle}>
        {/* <h2 className={groupNum === 3 ? "blue-bg white container text-centered" : groupNum === 1 ? 'violet-bg white container text-centered' : 'grad-bg white container text-centered'}>{evName}</h2> */}
        <h2 className={groupNum === 3 ? "blue container text-centered" : groupNum === 1 ? 'violet container text-centered' : 'violet-blue container text-centered'}>{evName}</h2>
        <hr />
        <div className="black">
            <p className="lecturer-name" style={{marginBottom: '.25em'}}>{profName}</p>
            <div className="flex-centered-vert">
                <img src={(groupNum === 3) ? clockBlue : clockViolet} alt="Иконка часов" className="clock-icon" />
                <p className={clockTextStyle}>{time}</p>
            </div>
            <span className="pointer ev-more" onClick={() => setOpen(open => !open)}>Подробнее</span>
        </div>
    </animated.div>;

    return <animated.div style={{ ...propsSpring, width: 250, height: 250, background: 'violet' }} onClick={() => setOpen(open => !open)}>
        { transitions.map(({ item, key, props }) => (
        <animated.div key={key} style={props}>
            <div>
                {info[key].name}
            </div>
        </animated.div>
        )) }
    </animated.div>;

    return <div className={cardStyle}>
        <h2>{evName}</h2>
        <hr />
        <div style={{color: 'black'}}>
            <p style={{marginBottom: '.25em'}}>{profName}</p>
            <div className="flex-centered-vert">
                <img src={(groupNum === 2) ? clockBlue : clockViolet} alt="Иконка часов" className="clock-icon" />
                <p className="align-with-clock" className={clockTextStyle}>{time}</p>
            </div>
            <span className="pointer ev-more">Подробнее...</span>
        </div>
    </div>;

    // return (
    //     <div className={cardStyle}>
    //         <h2 style={{fontSize: '1.1em'}}>{evName}</h2>
    //         <hr />
    //         <div style={{color: 'black'}}>
    //         <p><span>М. В. Торопыгина</span> (ИКВиА НИУ ВШЭ)</p>
    //         <div className="flex-centered-vert">
    //             <div className={clockStyle} />
    //             <div className="align-with-clock" className={clockTextStyle}>10:00 - 11:00</div>
    //         </div>
    //         </div>
    //     </div>
    // );
}

export default EventCard;