import React from 'react';
import { Link, animateScroll as scroll } from 'react-scroll';

import logoNav from '../../assets/images/logo-nav.svg';
import DrawerToggleButton from '../SideDrawer/DrawerToggleButton';
import Donate from './Donate';
import './Nav.css';

const Nav = ({ drawerToggleClickHandler, show }) => {

    let headerStyle = 'sticky white-bg full-width flex-centered-vert border-radius';
    if (!show) {
        headerStyle = 'header-hidden white-bg full-width flex-centered-vert border-radius';
    }

    return (
        <header className={headerStyle}>
            <a onClick={() => scroll.scrollToTop({duration: 500})}>
                <img src={logoNav} alt="Logo" className="nav-logo pointer" />
            </a>
            <nav>
                <div className="toggle-btn">
                    <Donate />
                    <div onClick={drawerToggleClickHandler}>
                        <DrawerToggleButton />
                    </div>
                </div>
                <ul>
                    <a onClick={() => scroll.scrollToTop({duration: 500})}>
                        <li className="pointer">главная</li>
                    </a>
                    <Link activeClass="active" to="about" spy={true} smooth={true} offset={0} duration={500}>
                        <li className="pointer">о школе</li>
                    </Link>
                    <Link activeClass="active" to="events" spy={true} smooth={true} offset={0} duration={500}>
                        <li className="pointer">программа</li>
                    </Link>
                    <Link activeClass="active" to="faq" spy={true} smooth={true} offset={0} duration={500}>
                        <li className="pointer">FAQ</li>
                    </Link>
                    <Link activeClass="active" to="contacts" spy={true} smooth={true} offset={0} duration={500} style={{marginRight: '.5em'}}>
                        <li className="pointer">контакты</li>
                    </Link>
                    {/* <a onClick={() => scroll.scrollToTop({duration: 500})}>
                        <li>внести<br />взнос</li>
                    </a> */}
                    <Donate />
                </ul>
            </nav>
        </header>
    );
}

export default Nav;