import React, { useState, useEffect } from 'react';
import { useSwipeable } from 'react-swipeable'

import EventCard from './EventCard';
import leftArrow from '../../assets/images/arrow-left.svg';
import rightArrow from '../../assets/images/arrow-right.svg';
import './Event.css';

const events = [
    {
        name: 'Открытие',
        day: 'Воскресенье',
        date: '27 Октября 2019',
        firstGroup: [
            
        ]
    },
    {
        name: <span>Первый<br />день</span>,
        day: 'Суббота',
        date: '2 Ноября 2019',

    },
    {
        name: <span>Последний<br />день</span>,
        day: 'Воскресенье',
        date: '3 Ноября 2019',
    },
];

const Event = ({ num, arrowClicked }) => {

    const [windowWidth, setWindowWidth] = useState(window.innerWidth);
    const [selectedGroup, setSelectedGroup] = useState(1);

    const updateWindowDimensions = () => {
        setWindowWidth(window.innerWidth);
    }

    useEffect(() => {
        window.addEventListener('resize', updateWindowDimensions);
        return () => {
            window.removeEventListener('resize', updateWindowDimensions);
        }
    });

    const handleRightSwipe = () => {
        if (num > 0) {
            arrowClicked(false);
        }
        
    }

    const handleLeftSwipe = () => {
        if (num < 2) {
            arrowClicked(true);
        }
    }

    let isDesktop = windowWidth > 950;

    let firstGroupLabelStyle = 'violet';
    let secondGroupLabelStyle = 'violet-blue';
    let thirdGroupLabelStyle = 'blue';

    if (!isDesktop) {
        if (selectedGroup === 1) {
            firstGroupLabelStyle = 'white pointer violet-bg padded-container';
            secondGroupLabelStyle += ' pointer padded-container';
            thirdGroupLabelStyle += ' pointer padded-container';
        } else if (selectedGroup === 2) {
            secondGroupLabelStyle = 'white pointer grad-bg padded-container';
            firstGroupLabelStyle += ' pointer padded-container';
            thirdGroupLabelStyle += ' pointer padded-container';
        } else {
            thirdGroupLabelStyle = 'white pointer blue-bg padded-container';
            firstGroupLabelStyle += ' pointer padded-container';
            secondGroupLabelStyle += ' pointer padded-container';
        }
    }

    const handlers = useSwipeable({ onSwipedRight: (eventData) => handleRightSwipe(), onSwipedLeft: (eventData) => handleLeftSwipe() })

    // style={{padding: '0 85px'}}

    return (
        <div className="events-container relative margin-from-prev-comp" {...handlers}>
            { num > 0 ?
                <img src={leftArrow} className="arrow arrow-left pointer" alt="Левая стрелка" onClick={() => arrowClicked(false)} /> :
                <div className="hide" /> }
            { num < 2 ? 
                <img src={rightArrow} className="arrow arrow-right pointer" alt="Правая стрелка" onClick={() => arrowClicked(true)} /> :
                <div className="hide" /> }
            <div className="events-header">
                <h2 className="flex-column text-centered white ev-header-style">{events[num].name}</h2>
                <p className="flex-column text-centered white ev-header-style">{events[num].date}<br />{events[num].day}</p>
            </div>
            <div className="flex-column">
                <div className="ev-cards ev-padding-label">
                    <div className={"group-label ev-right-margin text-centered " + firstGroupLabelStyle} onClick={() => setSelectedGroup(1)}>1 Поток</div>
                    <div className={"group-label ev-right-margin text-centered " + secondGroupLabelStyle} onClick={() => setSelectedGroup(2)}>2 Поток</div>
                    <div className={"group-label ev-right-margin text-centered " + thirdGroupLabelStyle} onClick={() => setSelectedGroup(3)}>3 Поток</div>
                </div>
                <div className="ev-cards ev-font-padding">
                    { (isDesktop || selectedGroup === 1) ? <EventCard 
                        evName='Лекция / интерактивная дискуссия по ведущим святилищам синто в Японии в Средние века'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={1} /> : <div className="hide" /> }
                    { (isDesktop || selectedGroup === 2) ? <EventCard 
                        evName='Лекция о системе международных отношений'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={2} /> : <div className="hide" /> }
                    { (isDesktop || selectedGroup === 3) ? <EventCard 
                        evName='Лекция о системе международных отношений на Дальнем Востоке в Средние века'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={3} /> : <div className="hide" /> }
                    
                </div>
                <div className="ev-cards ev-font-padding">
                    { (isDesktop || selectedGroup === 1) ? <EventCard 
                        evName='Лекция / интерактивная дискуссия по ведущим святилищам синто в Японии в Средние века'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={1} /> : <div className="hide" /> }
                    { (isDesktop || selectedGroup === 2) ? <EventCard 
                        evName='Лекция о системе международных отношений на Дальнем Востоке в Средние века'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={2} /> : <div className="hide" /> }
                    { (isDesktop || selectedGroup === 3) ? <EventCard 
                        evName='Лекция о системе международных отношений на Дальнем Востоке в Средние века'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={3} /> : <div className="hide" /> }
                </div>
                <div className="ev-cards ev-font-padding">
                    { (isDesktop || selectedGroup === 1) ? <EventCard 
                        evName='Лекция о Японии'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={1} /> : <div className="hide" /> }
                    { (isDesktop || selectedGroup === 2) ? <EventCard 
                        evName='Лекция о системе международных отношений на Дальнем Востоке в Средние века'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={2} /> : <div className="hide" /> }
                    { (isDesktop || selectedGroup === 3) ? <EventCard 
                        evName='Лекция о системе международных отношений на Дальнем Востоке в Средние века'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={3} /> : <div className="hide" /> }
                </div>
                <div className="ev-cards ev-font-padding">
                    { (isDesktop || selectedGroup === 1) ? <EventCard 
                        evName='Лекция1'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={1} /> : <div className="hide" /> }
                    { (isDesktop || selectedGroup === 2) ? <EventCard 
                        evName='Лекция2'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={2} /> : <div className="hide" /> }
                    { (isDesktop || selectedGroup === 3) ? <EventCard 
                        evName='Лекция2'
                        profName='М. В. Торопыгина (ИКВиА НИУ ВШЭ)'
                        time='10:00 - 11:00'
                        groupNum={3} /> : <div className="hide" /> }
                </div>
            </div>
        </div>
    );
}

export default Event;